.canvasSection {
  width: 100%;
  -ms-flex-order: 1;
      order: 1;
}

#canvas {
  display: block;
  width: 100%;
  height: 150px;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
  border: 1px solid #676767;
  border-radius: 5px;
}

#canvas.fail {
  background-color: #e6b7a9;
}

@media only screen
and (min-width : 600px) {
  .canvasSection {
    -ms-flex-order: 2;
        order: 2;   
  }
  #canvas {
    width: 100%;
    height: 350px;
    margin: 1em;
  }  
}



